import React, { Component } from 'react';

import PropTypes from 'prop-types';


import slot from '../../Common/Util/slot';

import {
	handleWaveMouseDown,
	handleWaveMouseUp,
	handleWaveMouseLeave,
} from './Wave';

import Tooltip, { theme as tooltipTheme,
	handleTooltipContainerMouseDown,
	handleTooltipContainerMouseEnter,
	handleTooltipContainerMouseLeave,
	handleTooltipContainerFocus,
	handleTooltipContainerBlur,
} from '../Tooltip/Tooltip';

import classNames from 'classnames';

import './scss/icon-button.css';
import './scss/icon-effects.css';

export const theme = {
	CLASSES: 'rmd-icon-btn',
	CLASSES_WRAPPER: 'rmd-icon-btn--wrapper',
	CLASSES_TOOLTIP: 'rmd-icon-btn--tooltip',

	CLASSES_WAVE: 'rmd-icon-btn--wave',
	CLASSES_CONTENT: 'rmd-icon-btn--content',

	CLASS_ACTIVE: 'rmd-icon-btn__active',
	CLASS_ACTIVE_FINISH: 'rmd-icon-btn__active-finish',
	CLASS_ACTIVE_RESET: 'rmd-icon-btn__active-reset',

	CLASSES_DARK: 'rmd-icon-btn__dark',

	CLASSES_TOOLTIP_CONTAINER: tooltipTheme.CLASSES_TOOLTIP_CONTAINER,
	CLASS_TOOLTIP_CONTAINER_FOCUS: tooltipTheme.CLASS_TOOLTIP_CONTAINER_FOCUS,
	CLASS_TOOLTIP_CONTAINER_ACTIVE: tooltipTheme.CLASS_TOOLTIP_CONTAINER_ACTIVE,

	ANIMATION_TIME_MIN: 400,

	TOOLTIP_HIDE_ANIMATION_TIME: 450 + 200,
};


class IconButton extends Component {
	constructor(props) {
		super(props);

		this.theme = theme;

		this._refs = {
			$button: undefined,
			$wrapper: undefined,
		};

		this._forceFocus = {
			mouseDown: false,
			focus: false,
		};

		this.handleButtonRef = this.handleButtonRef.bind(this);
		this.handleWrapperRef = this.handleWrapperRef.bind(this);

		this.handleMouseDown = this.handleMouseDown.bind(this);
		this.handleMouseUp = this.handleMouseUp.bind(this);
		this.handleMouseLeave = this.handleMouseLeave.bind(this);
		this.handleFocus = this.handleFocus.bind(this);
		this.handleBlur = this.handleBlur.bind(this);

		this.handleTooltipContainerMouseEnter = this.handleTooltipContainerMouseEnter.bind(this);
		this.handleTooltipContainerMouseLeave = this.handleTooltipContainerMouseLeave.bind(this);
		this.handleTooltipContainerMouseDown = this.handleTooltipContainerMouseDown.bind(this);
	}

	forceFocus(event) {
		if (event === 'mouseDown') {
			this._forceFocus.mouseDown = true;
		} else if (event === 'focus') {
			this._forceFocus.focus = true;
		}

		setTimeout(() => {
			if (this._forceFocus.mouseDown && ! this._forceFocus.focus) {
				if (this._refs.$button && document.activeElement !== this._refs.$button) {
					// for focus wave effect
					this._refs.$button.dataset.mouseFocus = '1';

					this._refs.$button.focus();
				}

			}

			this._forceFocus.mouseDown = false;
			this._forceFocus.focus = false;
		}, 0);
	}

	handleMouseDown(e) {
		const { _refs: refs, props, theme } = this;

		handleWaveMouseDown(refs, props, theme, e);

		if (refs.$button) {
			const $button = refs.$button;

			// for focus wave effect
			$button.dataset.mouseFocus = '1';
		}

		// FF on Mac, Safari bug
		this.forceFocus('mouseDown');

		if (props.onMouseDown) {
			return props.onMouseDown(e);
		}
	};


	handleMouseUp(e) {

		const { _refs: refs, props, theme } = this;

		handleWaveMouseUp(refs, props, theme, e);

		if (props.onMouseUp) {
			return props.onMouseUp(e);
		}
	};

	handleMouseLeave(e) {

		const { _refs: refs, props, theme } = this;

		handleWaveMouseLeave(refs, props, theme, e);

		if (props.onMouseLeave) {
			return props.onMouseLeave(e);
		}
	};

	handleFocus(e) {
		const { _refs: refs, props, theme } = this;

		handleTooltipContainerFocus(refs, props, theme, e);

		// FF on Mac, Safari bug
		this.forceFocus('focus');

		if (props.onFocus) {
			return props.onFocus(e);
		}
	};

	handleBlur(e) {
		const { _refs: refs, props, theme } = this;

		if (refs.$button) {
			const $button = refs.$button;

			if ($button.dataset.mouseFocus) {
				delete $button.dataset.mouseFocus;
			}
		}

		handleTooltipContainerBlur(refs, props, theme, e);

		if (props.onBlur) {
			return props.onBlur(e);
		}
	};

	handleTooltipContainerMouseEnter(e) {
		handleTooltipContainerMouseEnter(this._refs, this.props, this.theme, e);
	}

	handleTooltipContainerMouseLeave(e) {
		handleTooltipContainerMouseLeave(this._refs, this.props, this.theme, e);
	}

	handleTooltipContainerMouseDown(e) {
		handleTooltipContainerMouseDown(this._refs, this.props, this.theme, e);
	}

	handleButtonRef(el) {
		this._refs.$button = el;
	}

	handleWrapperRef(el) {
		this._refs.$wrapper = el;
	}

	render() {

		const { style, dark, tooltipPosition, tooltipClass, tooltipWithTriangle, children, ...props  } = this.props;

		const _theme = this.theme;

		const { main, tooltip } = slot(children);

		const button = ({ style } = {}) => (
			<button
				{ ...props }
				style={ style || props.btnStyle }
				className={ classNames(_theme.CLASSES, props.class, props.className, props.btnClass, {
					[ _theme.CLASSES_DARK ]: dark,
				}) }
				ref={ this.handleButtonRef }
				onMouseDown={ this.handleMouseDown }
				onMouseUp={ this.handleMouseUp }
				onMouseLeave={ this.handleMouseLeave }
				onFocus={ this.handleFocus }
				onBlur={ this.handleBlur }
			>
				<div className={ _theme.CLASSES_WAVE }></div>
				<div className={ _theme.CLASSES_CONTENT }>
					{ main }
				</div>
			</button>
		);

		if ( ! tooltip ) {
			return button({ style });
		}

		return (
			<div
				style={ style || props.wrapperStyle }
				className={ classNames(_theme.CLASSES_WRAPPER, _theme.CLASSES_TOOLTIP_CONTAINER, props.wrapperClass) }
				ref={ this.handleWrapperRef }
				onMouseEnter={ this.handleTooltipContainerMouseEnter }
				onMouseLeave={ this.handleTooltipContainerMouseLeave }
				onMouseDown={ this.handleTooltipContainerMouseDown }
			>
				{ tooltip ?
					<Tooltip
						position={ tooltipPosition }
						className={ classNames(_theme.CLASSES_TOOLTIP, tooltipClass) }
						withTriangle={ tooltipWithTriangle }
					>{ tooltip }</Tooltip>
					: null
				}
				{ button() }
			</div>
		);
	}
}

IconButton.propTypes = {
	dark: PropTypes.bool,
	tooltipPosition: PropTypes.oneOf(['top', 'left', 'bottom', 'right']),
 	tooltipClass: PropTypes.string,
 	tooltipWithTriangle: PropTypes.bool,
 	btnStyle: PropTypes.any,
 	wrapperStyle: PropTypes.any,
 	btnClass: PropTypes.string,
 	wrapperClass: PropTypes.string,
};


export default IconButton;



